.workList {
   width: 100%;

   color: #fff;
   position: relative;

   margin-bottom: 4rem;
   .workListwrapper {
      max-width: 1400px;
      margin: 150px auto 0;
      display: flex;

      align-items: baseline;
      justify-content: space-between;
      h1 {
         font-weight: bold;
         font-size: 6rem;
      }
   }
   .ListMenu {
      ul {
         display: flex;
         font-size: 20px;

         a {
            padding: 0;
            margin: 0;
            display: block;
            padding-right: 40px;
            text-decoration: none;
            &:hover {
               color: #10a19d;
            }
         }
      }
   }
   .gridContainer {
      position: relative;
      width: 100%;
      max-width: 1400px;
      margin: 0 auto;
      ul {
         margin-top: 4rem;
         gap: 1rem;
         display: grid;
         grid-template-columns: repeat(3, 1fr);
         li {
            width: 100%;
            height: 100%;
            background-color: #ffffff;
            color: black;
            cursor: pointer;
            a {
               width: 100%;
               height: 100%;
            }
            figure {
               position: relative;
               overflow: hidden;
               text-align: center;
               display: inline-block;
               width: 100%;
               height: 100%;
               background-color: #223046;
               &:hover img {
                  opacity: 0.6;
                  transform: scale3d(1, 1, 1);
               }
               &:hover p {
                  opacity: 1;
                  transition-delay: 0.1s;
               }
               &:hover figcaption::before {
                  transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0, 100%, 0);
               }
               img {
                  width: 100%;
                  height: 100%;
                  opacity: 0.95;
                  transition: opacity 0.35s, transform 0.35s;
                  transform: scale3d(1.05, 1.05, 1);
               }

               figcaption {
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  backface-visibility: hidden;

                  &::before {
                     content: '';
                     position: absolute;
                     top: 0;
                     left: 0;
                     width: 100%;
                     height: 100%;
                     background-color: rgba(16, 161, 157, 0.7);
                     transition: transform 0.6s;
                     transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0, -100%, 0);
                  }
                  p {
                     color: rgb(255, 255, 255);
                     position: absolute;
                     right: 0;
                     bottom: 0;
                     margin: 3em;
                     padding-right: 1rem;
                     max-width: 150px;
                     border-right: 4px solid #fff;
                     text-align: right;
                     opacity: 0;
                     transition: opacity 0.35s;
                     z-index: 999;
                  }
                  a {
                     z-index: 1000;
                     text-indent: 200%;
                     white-space: nowrap;
                     font-size: 0;
                     opacity: 0;
                     width: 100%;
                     height: 100%;
                  }
                  h2 {
                     color: white;
                     margin: 3rem auto;
                     font-size: 2rem;
                  }
               }
            }
         }
      }
   }
}
